* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 110vh;
  display: flex;
  flex-direction: column;
}

body {
  font-family: "Nunito", sans-serif;
  background: rgb(217, 222, 247);
  background: linear-gradient(0deg, rgba(150, 168, 255, 0.28) 0, rgb(224, 227, 245) 100%);
  color: #343434;
}

.custom-navbar {
  width: 100%;
  background: rgb(53, 66, 134);
  background: linear-gradient(120deg, rgb(53, 66, 134) 0%, rgb(69, 92, 217) 100%);
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  padding: 10px 67px;
  color: #fff;
  z-index: 9999;
}
.custom-navbar img {
  width: 40px;
}
.custom-navbar .container-fluid .navbar-toggler {
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.custom-navbar .container-fluid .navbar-toggler:focus {
  outline: none;
  box-shadow: 0 0 0 0;
}
.custom-navbar .container-fluid .navbar-toggler img {
  display: none;
  width: 20px;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .container-fluid .navbar-toggler img {
    display: block;
  }
}
.custom-navbar .navbar-nav {
  margin-left: auto;
  width: 100%;
}
.custom-navbar .navbar-nav li {
  margin-right: 43px;
  border-radius: 5px;
  transition: background 0.2s;
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav li {
    margin-right: 0;
  }
}
.custom-navbar .navbar-nav li img {
  width: 20px;
}
.custom-navbar .navbar-nav li a {
  color: #fff !important;
  font-size: 16px;
  font-weight: 600;
}
.custom-navbar .navbar-nav li:hover {
  background: rgba(255, 255, 255, 0.11);
}
.custom-navbar .navbar-nav li .user-icon-link {
  display: flex;
  align-items: center;
}
.custom-navbar .navbar-nav .navbar-middle-part {
  margin-right: auto;
  margin-left: auto;
  display: flex;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .navbar-middle-part {
    flex-direction: column;
    align-items: center;
  }
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .navbar-middle-part .nav-item {
    margin: 0 0 5px 0;
  }
}
.custom-navbar .navbar-nav .input-nav-link {
  margin-right: 0;
}
.custom-navbar .navbar-nav .input-nav-link:hover {
  background: none;
}
@media only screen and (max-width: 991px) {
  .custom-navbar .navbar-nav .input-nav-link {
    justify-content: center;
  }
}
@media only screen and (max-width: 400px) {
  .custom-navbar {
    padding: 10px;
  }
}

.nav-search-wrapper {
  position: relative;
  width: 100%;
  max-width: 231px;
}
.nav-search-wrapper input {
  background-color: #fff;
  border: none;
  outline: none;
  color: #455cd9;
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 14px;
  width: 100%;
}
.nav-search-wrapper input:focus {
  outline: none;
}
.nav-search-wrapper input::placeholder {
  color: #455cd9;
}
@media only screen and (max-width: 450px) {
  .nav-search-wrapper input::placeholder {
    color: transparent;
  }
}
.nav-search-wrapper img {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 17px;
}

.search-nav-right li {
  width: 100%;
}
.search-nav-right li .nav-search-wrapper {
  max-width: 100%;
}

.profile-link {
  position: relative;
}
@media only screen and (max-width: 991px) {
  .profile-link {
    width: 10%;
    margin: 4px auto !important;
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.profile-link .dropdown {
  animation: fade 0.3s ease-in-out;
  box-shadow: -2px 8px 77px 8px rgba(0, 0, 0, 0.35);
  -webkit-box-shadow: -2px 8px 77px 8px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: -2px 8px 77px 8px rgba(0, 0, 0, 0.35);
  height: 70px;
  width: 120px;
  position: absolute;
  top: 100%;
  right: -120%;
  background-color: rgba(255, 255, 255, 0.692);
  color: #455cd9;
  padding: 5px;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
}
.profile-link .dropdown button {
  font-weight: bold;
  border: none;
  background-color: transparent;
  color: #455cd9;
}
.profile-link .dropdown ul {
  transform: translate(-10%);
  padding: 0;
}
.profile-link .dropdown a {
  text-decoration: none;
}
.profile-link .dropdown a span {
  color: #455cd9 !important;
  font-weight: bold;
}
.profile-link .dropdown li {
  padding-left: 15px;
  text-decoration: none;
  text-align: center;
  margin: 0 auto;
  width: 120%;
}
@media only screen and (max-width: 991px) {
  .profile-link .dropdown {
    position: absolute;
    align-items: center;
    justify-content: center;
    top: 220%;
    right: -50px;
  }
}
@media only screen and (max-width: 991px) {
  .profile-link {
    justify-content: center;
    margin-bottom: 10px;
  }
}

.Footer {
  background-color: #efefef;
  margin-top: auto;
  min-height: 40%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.Footer .sponsor-scroll::after {
  content: "";
  display: block;
  width: 90%;
  border-bottom: solid 1px rgba(69, 92, 217, 0.5);
  margin: 0 auto;
  padding-bottom: 1%;
}
.Footer .sponsor-scroll {
  min-height: 30%;
  padding: 2% 0;
}
@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
.Footer .sponsor-scroll .logos {
  overflow: hidden;
  height: 50px;
  white-space: nowrap;
  position: relative;
}
.Footer .sponsor-scroll .logos:before,
.Footer .sponsor-scroll .logos:after {
  position: absolute;
  top: 0;
  width: 20%;
  height: 100%;
  content: "";
  z-index: 2;
}
.Footer .sponsor-scroll .logos:before {
  left: 0;
  background: linear-gradient(to left, rgba(255, 255, 255, 0), #efefef);
}
.Footer .sponsor-scroll .logos:after {
  right: 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #efefef);
}
.Footer .sponsor-scroll .logos:hover .logos-slide {
  animation-play-state: paused;
}
.Footer .sponsor-scroll .logos-slide {
  display: inline-block;
  animation: 25s slide infinite linear;
}
.Footer .sponsor-scroll .logos-slide img {
  height: 50px;
  margin: 0 50px;
}
.Footer .info-panel {
  max-height: 70%;
  padding: 2% 0%;
  overflow: hidden;
  margin: 0 auto;
  display: grid;
  width: 100%;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: auto;
  align-items: center;
  justify-items: center;
  gap: 15px;
}
@media (max-width: 767px) {
  .Footer .info-panel {
    transform: scale(0.9);
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
  }
}
.Footer .info-panel ul {
  font-weight: 600;
  text-align: center;
  font-size: 100%;
  color: #343434;
  padding: 0;
  margin: 0;
  list-style-type: none;
  width: 100%;
}
.Footer .info-panel .social-icons {
  display: grid;
  gap: 10px;
  justify-content: space-around;
}
@media (max-width: 767px) {
  .Footer .info-panel .social-icons {
    width: 80%;
    gap: 15px;
    grid-template-columns: repeat(4, 1fr);
    grid-column: 1/-1;
  }
}

