.login-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.753);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 0.15s ease-in;
}
.login-screen .container {
  color: #fff;
  position: relative;
  background-color: rgba(69, 91, 217, 0.8);
  height: auto;
  width: 500px;
  border-radius: 10px;
}
.login-screen .container .close-btn {
  position: absolute;
  right: 2%;
  top: 2%;
  background-color: transparent;
  border: none;
  transform: scale(0.75);
}
.login-screen .container h2 {
  margin: 10%;
  text-align: center;
  font-size: 26px;
}
.login-screen .container h2::after {
  content: "";
  display: block;
  width: 70%;
  border-bottom: solid 1px rgb(255, 255, 255);
  margin: 0 auto;
  padding-bottom: 5%;
}
.login-screen .container .text-section {
  margin: 0 auto;
  max-width: 75%;
}
.login-screen .container .text-section form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
}
.login-screen .container .text-section form .kep-login-facebook {
  border-radius: 15px;
  font-size: inherit;
  font-family: "Nunito", sans-serif;
  padding: 10px 20px;
  margin-bottom: 30px;
  background-color: #0d6efd;
  text-transform: none;
  font-weight: 600;
}
.login-screen .container .text-section form .register-input-wrapper {
  position: relative;
  margin-bottom: 20px;
}
.login-screen .container .text-section form .register-input-wrapper span,
.login-screen .container .text-section form .register-input-wrapper img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
}
.login-screen .container .text-section form .register-input-wrapper span {
  left: 15px;
  font-size: 15px;
  color: #ccd1f4;
  transition: transform 0.2s, font-size 0.2s;
  pointer-events: none;
}
.login-screen .container .text-section form .register-input-wrapper input {
  margin: 0;
}
.login-screen .container .text-section form .register-input-wrapper input:focus + span {
  transform: translate(-8px, -20px);
  font-size: 10px;
}
.login-screen .container .text-section form .register-input-wrapper .active-input {
  transform: translate(-8px, -20px);
  font-size: 10px;
}
.login-screen .container .text-section form input {
  color: white;
  background-color: #677adf;
  padding: 5px;
  border: none;
  border-radius: 10px;
  width: 100%;
  height: 43px;
}
.login-screen .container .text-section form input:focus {
  outline: none;
}
.login-screen .container .text-section form input::placeholder {
  font-size: 14px;
  font-weight: 600;
  padding-left: 10px;
  color: rgba(255, 255, 255, 0.7882352941);
}
.login-screen .container .text-section form .recaptcha {
  position: unset;
  width: unset;
  height: 80px;
  display: block;
}
.login-screen .container .text-section form div {
  position: relative;
  width: 100%;
  height: 43px;
}
.login-screen .container .text-section form div .input-img {
  position: absolute;
  width: 24px;
  right: 3%;
  top: 50%;
  transform: translateY(-50%);
}
.login-screen .container .text-section form img {
  width: 20px;
}
.login-screen .container .text-section form .other-login {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
}
.login-screen .container .text-section form .other-login .facebook img {
  width: 30px;
}
.login-screen .container .text-section form .other-login .facebook p {
  text-align: center;
  margin: 0;
  font-size: 14px;
  width: 60%;
}
.login-screen .container .text-section form p {
  margin: 0 15px 15px 15px;
}
.login-screen .container .text-section form p a {
  color: #f4cd46;
  text-decoration: none;
}
.login-screen .container .text-section form .login-btn {
  background-color: #f4cd46;
  padding: 10px 30px;
  font-weight: bold;
  border: none;
  border-radius: 50px;
  margin-bottom: 20px;
  color: #343434;
}
.login-screen .container .text-section form .multiple-inputs-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.login-screen .container .text-section form .multiple-inputs-wrapper .register-input-wrapper {
  width: 47%;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}